		-moz-transition:all 0.5s;
		-moz-transition:all 0.5s;
		-ms-transition:all 0.5s;
		-o-transition:all 0.5s;
	}
	.filter-left.filter-on{
		left: 0;
	}
	.filter-bar{
		top: 80px;
		position: -webkit-sticky;
		position: sticky;
		z-index: 999;
	}
	.filter-bar.sticky-top .side-bar{
		z-index:9;
		top:0;
		position:unset;
	}
	.sticky-top.filter-bar{
		z-index:99999999;
		top: 80px;
		position: -webkit-sticky;
		position: sticky;
	}
	.filter-left:after{
		content: "";
		position: fixed;
		width: 0;
		background: #000;
		height: 100%;
		z-index: -1;
		left: 0;
		top: 0;
		opacity: 0;
		transition:all 0.5s;
		-moz-transition:all 0.5s;
		-moz-transition:all 0.5s;
		-ms-transition:all 0.5s;
		-o-transition:all 0.5s;
	}
	.filter-left.filter-on:after{
		width: 1000px;
		left: 280px;
		opacity: 0.8;
	}
