.time-line .line-right-box{
	
	width: 40%;
	position:relative;
	z-index: 1;
}
.line-right-box .line-content-box,
.line-left-box .line-content-box{
	background: #ffffff none repeat scroll 0 0;
    border-radius: 12px;
    box-shadow: 1px 1px 62px 0 rgba(0, 0, 0, 0.1);
